import i18next from "i18next";
import HttpBackend from "i18next-http-backend";
import LanguageDetector from "i18next-browser-languagedetector";
import { initReactI18next } from "react-i18next";

//const apiKey = "n8mxcLqlJSIrb1WjrtBVgA";
const apiKey = "sBXlLxNVPH8-uL1HjFjg_A";
const loadPath = `https://api.i18nexus.com/project_resources/translations/{{lng}}/{{ns}}.json?api_key=${apiKey}`;

i18next
  .use(HttpBackend)
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    fallbackLng: "en",

    ns: ["default"],
    defaultNS: "default",

    supportedLngs: ["en","af","ar","cs","hr","my","da","ka","el","hi","hu","id","it","es","pt","fa","pl","ms","tr","uk","th","sv","ro","fi","ga","is","nl","vi","ru","de","ja","fr","ko","he","pa","zh-TW","zh-CN"],
    backend: {
      loadPath: loadPath
    }
  })